"use client"

import { useState, useEffect } from "react"
import { useContract } from "@/context/contract-context"
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs"
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Skeleton } from "@/components/ui/skeleton"
import { Badge } from "@/components/ui/badge"
import { Input } from "@/components/ui/input"
import { 
  Bell, 
  Filter, 
  RefreshCw, 
  AlertCircle, 
  Search, 
  ChevronUp, 
  ChevronDown, 
  CalendarClock,
  AlertTriangle,
  Clock,
  User
} from "lucide-react"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuCheckboxItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import NotificationItem from "./_components/notification-item"
import EmptyState from "./_components/empty-state"

// Define NotificationType enum if it's missing
const NotificationType = {
  CAMPAIGN_CREATED: 0,
  CAMPAIGN_STARTED: 1,
  CAMPAIGN_ENDED: 2,
  VOTE_CAST: 3,
  VOTE_RECEIVED: 4,
  USER_REGISTERED: 5,
  CANDIDATE_REGISTERED: 6
}

export default function ActivityPage() {
  const { contract, address, isConnected, connect } = useContract()
  const [transactions, setTransactions] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [activeTab, setActiveTab] = useState("all")
  const [viewMode, setViewMode] = useState("all") // "all" or "myActivity"
  const [page, setPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)
  const [refreshing, setRefreshing] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")
  const [sortOrder, setSortOrder] = useState("desc") // desc = newest first, asc = oldest first
  const [selectedTypes, setSelectedTypes] = useState([
    "CAMPAIGN_CREATED", 
    "CAMPAIGN_STARTED", 
    "CAMPAIGN_ENDED", 
    "VOTE_CAST", 
    "VOTE_RECEIVED", 
    "USER_REGISTERED", 
    "CANDIDATE_REGISTERED"
  ])
  const ITEMS_PER_PAGE = 10

  const fetchTransactions = async (reset = false) => {
    try {
      const isRefresh = reset && transactions.length > 0
      if (isRefresh) {
        setRefreshing(true)
      } else {
        setLoading(true)
      }
      setError(null)

      const newPage = reset ? 1 : page
      const offset = (newPage - 1) * ITEMS_PER_PAGE

      let fetchedTransactions = []
      let totalCount = 0

      if (viewMode === "myActivity" && address) {
        // Get user's notifications with pagination
        totalCount = await contract.getUserNotificationCount(address)
        fetchedTransactions = await contract.getUserNotifications(address, offset, ITEMS_PER_PAGE)
      } else {
        // Get all transactions/activities
        totalCount = await contract.getAllTransactionsCount()
        fetchedTransactions = await contract.getAllTransactions(offset, ITEMS_PER_PAGE)
      }

      // Process transactions
      const processedTransactions = fetchedTransactions.map((transaction) => ({
        id: Number(transaction.id),
        userAddress: transaction.userAddress,
        campaignId: Number(transaction.campaignId),
        notificationType: Number(transaction.notificationType),
        timestamp: Number(transaction.timestamp) * 1000, // Convert to milliseconds
        relatedEntityId: Number(transaction.relatedEntityId),
        data: transaction.data,
      }))

      // Update state
      if (reset) {
        setTransactions(processedTransactions)
      } else {
        setTransactions((prev) => [...prev, ...processedTransactions])
      }

      setPage(newPage + 1)
      setHasMore(offset + ITEMS_PER_PAGE < Number(totalCount))
      
    } catch (err) {
      console.error("Error fetching transactions:", err)
      setError("Failed to load activity data. Please try again.")
    } finally {
      setLoading(false)
      setRefreshing(false)
    }
  }

  useEffect(() => {
    if (contract) {
      fetchTransactions(true)
    } else {
      setTransactions([])
      setLoading(false)
    }
  }, [contract, viewMode, address])

  const handleRefresh = () => {
    fetchTransactions(true)
  }

  const handleLoadMore = () => {
    if (!loading && hasMore) {
      fetchTransactions()
    }
  }

  const handleSortOrderChange = () => {
    setSortOrder(sortOrder === "desc" ? "asc" : "desc")
  }

  const handleTypeToggle = (type) => {
    setSelectedTypes((current) => 
      current.includes(type) 
        ? current.filter(t => t !== type)
        : [...current, type]
    )
  }

  const resetFilters = () => {
    setSelectedTypes([
      "CAMPAIGN_CREATED", 
      "CAMPAIGN_STARTED", 
      "CAMPAIGN_ENDED", 
      "VOTE_CAST", 
      "VOTE_RECEIVED", 
      "USER_REGISTERED", 
      "CANDIDATE_REGISTERED"
    ])
    setSearchQuery("")
  }

  const filterTransactions = (type) => {
    // First filter by tab category
    let filtered = transactions.filter((transaction) => {
      // Apply type filter from selected checkboxes
      const typeEnumValue = Object.entries(NotificationType)
        .find(([key]) => key === selectedTypes.find(t => t === key))
      
      if (typeEnumValue && !selectedTypes.includes(
        Object.keys(NotificationType)[transaction.notificationType]
      )) {
        return false
      }

      // Apply search filter
      if (searchQuery) {
        const searchLower = searchQuery.toLowerCase()
        const matchesSearch = 
          (transaction.data && transaction.data.toLowerCase().includes(searchLower)) ||
          String(transaction.campaignId).includes(searchLower) ||
          transaction.userAddress.toLowerCase().includes(searchLower)
        
        if (!matchesSearch) return false
      }

      switch (type) {
        case "campaigns":
          return [
            NotificationType.CAMPAIGN_CREATED,
            NotificationType.CAMPAIGN_STARTED,
            NotificationType.CAMPAIGN_ENDED,
          ].includes(transaction.notificationType)
        case "votes":
          return [NotificationType.VOTE_CAST, NotificationType.VOTE_RECEIVED].includes(transaction.notificationType)
        case "registrations":
          return [NotificationType.USER_REGISTERED, NotificationType.CANDIDATE_REGISTERED].includes(
            transaction.notificationType,
          )
        default:
          return true
      }
    })

    // Apply sorting
    filtered.sort((a, b) => {
      if (sortOrder === "desc") {
        return b.timestamp - a.timestamp
      } else {
        return a.timestamp - b.timestamp
      }
    })

    return filtered
  }

  const getActiveFiltersCount = () => {
    let count = 0
    if (searchQuery) count++
    if (selectedTypes.length < 7) count++
    if (sortOrder === "asc") count++
    return count
  }

  const getNotificationTypeName = (type) => {
    return Object.keys(NotificationType)[type]
  }

  return (
    <div className="container max-w-5xl py-10">
      <Card>
        <CardHeader className="flex flex-row items-center justify-between">
          <div>
            <CardTitle className="flex items-center gap-2">
              <CalendarClock className="h-5 w-5" />
              Activity Log
            </CardTitle>
            <CardDescription>View all transactions and activity in the application</CardDescription>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" size="sm" onClick={() => setViewMode(viewMode === "all" ? "myActivity" : "all")}>
              {viewMode === "all" ? (
                <>
                  <User className="h-4 w-4 mr-2" />
                  My Activity
                </>
              ) : (
                <>
                  <Bell className="h-4 w-4 mr-2" />
                  All Activity
                </>
              )}
            </Button>
            <Button variant="outline" size="icon" onClick={handleRefresh} disabled={loading || refreshing}>
              <RefreshCw className={`h-4 w-4 ${(loading || refreshing) ? "animate-spin" : ""}`} />
              <span className="sr-only">Refresh</span>
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col sm:flex-row gap-4 mb-6">
            <div className="relative flex-1">
              <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search by campaign ID, address, or content..."
                className="pl-9"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
            <div className="flex gap-2">
              <Button 
                variant="outline" 
                size="icon"
                onClick={handleSortOrderChange} 
                className={sortOrder === "asc" ? "bg-muted/50" : ""}
              >
                {sortOrder === "desc" ? (
                  <ChevronDown className="h-4 w-4" />
                ) : (
                  <ChevronUp className="h-4 w-4" />
                )}
                <span className="sr-only">Sort Order</span>
              </Button>
              
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="outline" size="icon" className={getActiveFiltersCount() > 0 ? "bg-muted/50" : ""}>
                    <Filter className="h-4 w-4" />
                    {getActiveFiltersCount() > 0 && (
                      <span className="absolute -top-1 -right-1 bg-primary text-primary-foreground rounded-full text-xs w-4 h-4 flex items-center justify-center">
                        {getActiveFiltersCount()}
                      </span>
                    )}
                    <span className="sr-only">Filter</span>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end" className="w-56">
                  <div className="px-3 py-2 text-sm font-medium">Filter Activity Types</div>
                  {Object.entries(NotificationType).map(([key, value]) => (
                    <DropdownMenuCheckboxItem
                      key={key}
                      checked={selectedTypes.includes(key)}
                      onCheckedChange={() => handleTypeToggle(key)}
                    >
                      {key.split('_').map(word => word.charAt(0) + word.slice(1).toLowerCase()).join(' ')}
                    </DropdownMenuCheckboxItem>
                  ))}
                  <DropdownMenuSeparator />
                  <div className="px-3 py-2">
                    <Button variant="ghost" size="sm" className="w-full" onClick={resetFilters}>
                      Reset Filters
                    </Button>
                  </div>
                </DropdownMenuContent>
              </DropdownMenu>
            </div>
          </div>

          <div className="mb-6">
            <Tabs defaultValue="all" value={activeTab} onValueChange={setActiveTab}>
              <TabsList className="w-full sm:w-auto">
                <TabsTrigger value="all" className="flex-1 sm:flex-initial">All</TabsTrigger>
                <TabsTrigger value="campaigns" className="flex-1 sm:flex-initial">Campaigns</TabsTrigger>
                <TabsTrigger value="votes" className="flex-1 sm:flex-initial">Votes</TabsTrigger>
                <TabsTrigger value="registrations" className="flex-1 sm:flex-initial">Registrations</TabsTrigger>
              </TabsList>
            </Tabs>
          </div>

          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-2">
              <Badge variant="outline" className="flex items-center gap-1">
                <Clock className="h-3 w-3" />
                <span>{sortOrder === "desc" ? "Newest first" : "Oldest first"}</span>
              </Badge>
              {viewMode === "myActivity" && (
                <Badge variant="outline" className="flex items-center gap-1 bg-primary/10">
                  <User className="h-3 w-3" />
                  <span>My activity only</span>
                </Badge>
              )}
              {getActiveFiltersCount() > 0 && (
                <Badge variant="outline" className="flex items-center gap-1 bg-muted">
                  <Filter className="h-3 w-3" />
                  <span>{getActiveFiltersCount()} active {getActiveFiltersCount() === 1 ? 'filter' : 'filters'}</span>
                </Badge>
              )}
            </div>
            {(searchQuery || selectedTypes.length < 7 || sortOrder === "asc") && (
              <Button variant="ghost" size="sm" onClick={resetFilters} className="text-xs h-7">
                Reset all filters
              </Button>
            )}
          </div>
<Tabs>
          <TabsContent value="all" className="mt-0">
            {renderTransactionList(filterTransactions("all"))}
          </TabsContent>

          <TabsContent value="campaigns" className="mt-0">
            {renderTransactionList(filterTransactions("campaigns"))}
          </TabsContent>

          <TabsContent value="votes" className="mt-0">
            {renderTransactionList(filterTransactions("votes"))}
          </TabsContent>

          <TabsContent value="registrations" className="mt-0">
            {renderTransactionList(filterTransactions("registrations"))}
          </TabsContent>
          </Tabs>

          {error && (
            <div className="p-4 bg-red-50 dark:bg-red-900/10 border border-red-200 dark:border-red-900/20 rounded-lg mt-4 flex flex-col items-center text-center">
              <AlertCircle className="h-8 w-8 text-red-500 mb-2" />
              <p className="text-red-600 dark:text-red-400">{error}</p>
              <Button variant="outline" onClick={handleRefresh} className="mt-3">
                Try Again
              </Button>
            </div>
          )}
        </CardContent>
        
        {hasMore && !loading && transactions.length > 0 && (
          <CardFooter className="flex justify-center border-t pt-4">
            <Button variant="outline" onClick={handleLoadMore} disabled={loading} className="w-full sm:w-auto">
              {loading ? "Loading..." : "Load More Activities"}
            </Button>
          </CardFooter>
        )}
        
        {loading && !refreshing && (
          <CardFooter className="border-t pt-4">
            <div className="space-y-4 w-full">
              {[...Array(3)].map((_, i) => (
                <div key={i} className="flex items-start gap-4">
                  <Skeleton className="h-10 w-10 rounded-full flex-shrink-0" />
                  <div className="space-y-2 flex-1">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-3/4" />
                    <div className="flex justify-between mt-2">
                      <Skeleton className="h-8 w-24" />
                      <Skeleton className="h-8 w-16" />
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </CardFooter>
        )}
      </Card>
    </div>
  )

  function renderTransactionList(filteredTransactions) {
    if (loading && transactions.length === 0) {
      return null // Loading state is handled separately
    }
  
    if (filteredTransactions.length === 0) {
      if (searchQuery || selectedTypes.length < 7) {
        return (
          <div className="flex flex-col items-center justify-center py-10 text-center rounded-lg border border-dashed">
            <AlertTriangle className="h-10 w-10 text-muted-foreground/50 mb-2" />
            <h3 className="text-lg font-medium">No results found</h3>
            <p className="text-muted-foreground text-sm mt-1">Try adjusting your search or filters</p>
            <Button variant="outline" className="mt-4" onClick={resetFilters}>
              Reset Filters
            </Button>
          </div>
        )
      }
      return <EmptyState type={activeTab} />
    }
  
    return (
      <div className="space-y-4">
        {filteredTransactions.map((transaction) => (
          <NotificationItem key={transaction.id} notification={transaction} />
        ))}
      </div>
    )
  }
}